<template>
  <div class="blogs">
    <div class="b-head"></div>
    <div class="bs-box">
      <div class="bs-head">
        <h1>{{blogs.name}}</h1>
        <p>{{stime(blogs.updated_at)}}</p>
      </div>
      <div class="bs-content" v-html="blogs.content.blocks[0].data.text"></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      blogs: {},
    };
  },
  methods: {
    stime(time) {
      this.$store.commit(time);
      setTimeout(function(){
        return this.$store.state.date;
      },1000)
    },
    getblog() {
      var _this = this;
      this.$axios({
        url:
          "/baklibapi/articles/" +
          _this.$route.params.id +
          "?tenant_id=a5e31530-0273-48ba-985d-3f425ab577c1",
        method: "GET",
        timeout: 0,
        headers: {
          Authorization:
            "Bearer 6e2b76bdf1493cbc7db23b57c3dedc75be40d0407230e2f022326ae54ae5adf5",
          "Content-Type": "application/json",
        },
      }).then(function (res) {
        _this.blogs = res.data.message;
        console.log(_this.blogs);
      });
    },
  },
  created() {
    this.getblog(1);
  },
};
</script>

<style>
.b-head {
  width: 100vw;
  height: 300px;
  background: linear-gradient(#1280ff, #297eff);
}
.bs-content {
  word-wrap: break-word;
}
.bs-box {
  width: 800px;
  margin: 30px auto;
  box-shadow: 0 10px 33px rgba(9, 16, 43, 0.15);
  padding: 20px;
}
.bs-head {
  line-height: 40px;
  margin-bottom: 20px;
}
</style>